.checkbox input[type="checkbox"] { display: none; }

.checkbox { display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; -webkit-flex-wrap: wrap; -ms-flex-wrap: wrap; flex-wrap: wrap; }

.checkbox label::before { content: ""; background: url("../img/icons/checkbox.png"); display: inline-block; height: 38px; width: 38px; cursor: pointer; }

.checkbox label::after { content: ""; margin-top: -14px; margin-left: -1px; cursor: pointer; }

.checkbox label { position: relative; }

.checkbox label::before, .checkbox label::after { position: absolute; }

/*Внешний блок*/
.checkbox label::before { top: 15px; }

/*Галка*/
.checkbox label::after { left: 10px; top: 39px; }

/*Прячем галку по умолчанию*/
.checkbox input[type="checkbox"] + label::after { content: none; }

/*Показываем галку по состоянию checked*/
.checkbox input[type="checkbox"]:checked + label::after { content: url("../img/icons/gulka.png"); }

